.hero{
    display: flex;
    justify-content: space-between;
}
.left-h{
    padding: 2rem;
    padding-top: 1.5em;
    flex:3;
    display: flex;
    gap: 2rem;
    flex-direction:column ;
}
.right-h{
    flex: 1;
    position: relative;
    background-color: var(--orange);
}
.the-best-ad{
    margin-top: 4rem;
    background-color: #363d42;
    border-radius: 4rem;
    width: fit-content;
    padding: 20px 13px;
    text-transform:uppercase ;
    color: rgb(53, 203, 23);
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-start;

}
.the-best-ad>span{
    z-index: 2;
}


.the-best-ad>div{
    position: absolute;
    background-color: aqua;
    width: 5.4rem;
    height: 80%;
    left: 8px;
    border-radius: 3rem;
    z-index: 1;
}
.hero-text{
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
    text-transform: uppercase;
    font-size: 4.5rem;
    font-weight: bold;
    color: white;
    text-overflow: inherit;

}

.hero-text>div:nth-of-type(3){
    font-size: 1rem;
    font-weight: 200;
    text-transform: none;
    letter-spacing: 1px;
    width:80% ;
}

.figures{
    display: flex;
    gap: 2rem;

}
.figures>div{
    display: flex;
    flex-direction: column;
}
.figures>div>span:nth-last-of-type(2){
    color: rgb(64, 138, 8);
    font-size: 2rem;

}

.figures>div>span:nth-last-of-type(1){
    color: rgb(27, 161, 159);
    text-transform:uppercase;

}
.hero-button{
    display: flex;
    gap: 1rem;
    font-weight: normal;
}
.hero-button>:nth-child(1){
    color: white;
    background-color: var(--orange);
    width: 8rem;
}
.hero-button>:nth-child(2){
    color: white;
    background-color:transparent;
    width: 8rem;
    border: 2px solid var(--orange) ;
}
.right-h>.btn{
    position: absolute;
    right: 3rem;top: 2rem;color: black;
}
.heart-rate{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: #495158;
    width:fit-content ;
    padding: 1rem;
    flex: start;
    border-radius: 5px;
    position: absolute;
    right: 15rem;
    top: 7rem;

}
.heart-rate>img{
    width: 2rem;
}
.heart-rate>span:nth-child(2){
    color: #363d42;
}
.heart-rate>span:nth-child(3){
    color: white;
    font-size: 1.5rem;
}
.hero-image{
    position: absolute;
    top: 10rem;
    right: 20rem;
    width: 23rem;
}
.hero-image-back{
    position: absolute;
    top: 4rem;
    right: 30rem;
    z-index: -1;
    width: 15rem;
}
.calories{
    display: flex;
    gap: 2rem;
    background-color: var(--caloryCard);
    border-radius: 5px;
    padding: 1rem;
    width: fit-content;
    position: absolute;
    top: 33rem;
    right: 67rem;
}
.calories > img{
    width: 3rem;
    height: 5rem;
    
    
}
.calories > div{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.calories > div > :nth-child(1){
    color: var(--gray);

}
.calories > div > :nth-child(2){
    color: white;
    font-size: 1.5rem;
}
.hero-blur{
    width: 22rem;
    height: 30rem;
    left: 0;
}
