.Programs {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    padding: 0.2px;
  }
  
  .programs-header {
    display: flex;
    gap: 2rem;
    font-weight: bold;
    font-size: 3rem;
    justify-content: center;
    color: rgb(255, 255, 255);
    text-transform: uppercase;
    font-style: italic;
  }
  .program-categories{
    display: flex;
    gap: 1rem;
  }
  .category{
   display: flex;
   flex-direction: column;
   background-color: rgb(45, 47, 46);
   padding: 2rem;
   gap: 1rem;
   color: white;
   justify-content: space-between;
  }
  .category>:nth-child(1){
    width: 2rem;
    height: 2rem;fill: white;
  }
  
  .category>:nth-child(2){
   font-size: 1.5rem;
   font-weight: bold;
  }


  .category>:nth-child(3){
    font-size: 1.5rem;
    line-height: 25px;
   }

   .join-now{
    display: flex;
    align-items: center; gap: 2rem;
   }

   .join-now>img{
    width: 1rem;
   }

   .category:hover{
    background: rgb(9, 175, 173);
    cursor: pointer;
   }